<template>
  <div class="orderwrite">
    <form action="/Web/Order/addorder.html" method="post" style='margin-bottom:60px' id="frmOrder">
            <!---->
            <div class="weui_cells_title recharge-title"></div>
            <div class="weui_cells weui_cells_form weui_cells_access">
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="type" v-model='type' type="text" id="type" placeholder="" class="weui_input weui-s must">
                    </div>
                    <div class="weui_cell_ft"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="account" v-model='account' type="text" id="account" maxlength="40" placeholder="" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="password" v-model='password' type="password" id="password" maxlength="40" placeholder="" class="weui_input must">
                    </div>
                    <div class="look_pwd look_pwd_off"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="password2" type="password" id="password2" maxlength="40" placeholder="" class="weui_input must">
                    </div>
                </div>
            </div>

            <!---->
            <div class="weui_cells_title recharge-title"></div>
            <div class="weui_cells weui_cells_access black_link">
              <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="game" v-model='game' type="text" id="game" maxlength="40" placeholder="" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="area" v-model='area' type="text" id="area" placeholder="" class="weui_input weui-s must">
                    </div>
                    <div class="weui_cell_ft"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="role" v-model='role' type="text" id="role" maxlength="40" placeholder="" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="price" v-model='price' type="text" id="price" maxlength="40" placeholder="" class="weui_input weui-s must">
                    </div>
                    <div class="weui_cell_ft"></div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="count" v-model='count' type="text" id="count" maxlength="40" placeholder="" class="weui_input must">
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_hd"><label class="weui_label"></label></div>
                    <div class="weui_cell_bd weui_cell_primary">
                        <input name="remark" v-model='more' id="remark" type="text" placeholder="100" class="weui_input">
                    </div>
                </div>
            </div>

            <p style='font-size:12px;padding:15px 15px'>IDID</p>
        </form>

        <!---->
        <div class="submit-f overflow fixed">
          <div class="left">
            <span class='total'>{{money}}</span>
            <span class='discount'>|8.64</span>
          </div>
          <span class="right chose" style='color:white'  @click='sub()'></span>
          <!-- <router-link :to="{name:'ordercomfirm',params:{money:money,game:game,type:type,account:account,area:area,role:role,more:more,password:password,count:count,total:total}}" class="right chose" style='color:white' ></router-link> -->
        </div>
  </div>
</template>

<script>
import router from '../../router.js'
export default {
  name: 'hello',
  created(){
    // 
    // console.log(1)
    // this.price='';this.game='';this.type='';this.account='';this.area='';this.role='';this.more='';this.password='';this.count='';
    // console.log(this.account);
    // weui
    var me=this;
    setTimeout(function(){
      // 
      var choseItem={
        data:[{
            item:['','QQ','',''],
            title:''
          },{
            item:['','',''],
            title:''
          },{
            item:['60','100','200'],
            title:''
          }],
        select:function(obj,data,idf)
        {
          obj.select({
                title: data.title,
                items: data.item,
                onChange: function(d) {
                  me[idf]=d.values;
                },
                onClose: function(d) {
                },
                onOpen: function(d) {
                },
              });
        }
      };
      $.each($('.weui-s'),function(index, value, array) {
         choseItem.select($(value),choseItem.data[index],$(value).attr('id'));
      });
    },0)


  },
  data () {
    return {
      price:'',game:'',type:'',account:'',area:'',role:'',more:'',password:'',count:''
    }
  },
  computed: {
    money:function(){
      var val=parseInt(this.count)*parseInt(this.price);
      // val? (function(){return val})():(function(){alert(1);return 0})();
      if(val){
        return val;
      }else{
        return 0
      }
      
    }
  },
  methods:{

    sub:function(){
      // 
      var isCompelet=true;
      $.each($('.must'), function(index, val) {
         if($(val).val()==''){
            isCompelet=false;
            // $.toast("", "forbidden");
            mui.alert('')
            return false;
         }
      });
      if(isCompelet){
        router.push({name:'ordercomfirm',params:{money:this.money,game:this.game,type:this.type,account:this.account,area:this.area,role:this.role,more:this.more,password:this.password,count:this.count,price:this.price}})
      }
    }

  }
}
</script>
<style>
input[type=color],input[type=date],input[type=datetime-local],input[type=datetime],input[type=email],input[type=month],input[type=number],input[type=password],input[type=search],input[type=tel],input[type=text],input[type=time],input[type=url],input[type=week],select,textarea {
    line-height: 21px;
    width: auto;
    height: auto;
    margin-bottom: 0px;
    padding: 0px 0px;
    -webkit-user-select: text;
    border: 0px solid rgba(0,0,0,.2);
    border-radius: 3px;
    outline: 0;
    background-color: transparent;
    -webkit-appearance: none;
}

</style>
<style>
  h1{
  margin:0;
}
</style>



