import Vue from 'vue'
import Content from './view/Content.vue'
import Home from './view/content/Home.vue'
import List from './view/content/List.vue'
import My from './view/content/My.vue'
import Cash from './view/content/Cash.vue'
import Order from './view/Order.vue'
import Question from './view/Question.vue'
import Mine from './view/Mine.vue'
import Charge from './view/mine/Charge.vue'
import massege from './view/mine/Massege.vue'
import chargeDetail from './view/mine/chargeDetail.vue'
import Orderinfo from './view/order/Orderinfo.vue'
import Ordertopay from './view/order/Ordertopay.vue'
import Orderwrite from './view/order/Orderwrite.vue'
import ordercomfirm from './view/order/ordercomfirm.vue'
import Allorder from './components/Allorder.vue'
import Payingorder from './components/Payingorder.vue'
import Waitingorder from './components/Waitingorder.vue'
import Compeletorder from './components/Compeletorder.vue'
import msgOrder from './components/msgOrder.vue'
import msgKefu from './components/msgKefu.vue'
import msgSys from './components/msgSys.vue'
import auth from './view/auth.vue'
import login from './view/auth/login.vue'
import register from './view/auth/register.vue'
import App from './App.vue'

import VueRouter from 'vue-router'

Vue.use(VueRouter)


//  redirect: { name: 'xxx' } /path/path/pathpathpath
const routes = [
					{path:'/',component: Content,redirect: { name: 'home' },name:'/',children:[
							{ path: 'home', name: 'home', component: Home },
							{ path: 'my', name: 'my', component: My },
							
							{ path: 'list', component: List,name:'list',redirect: { name: 'all' },children:[
									{ path: 'all', name: 'all', component: Allorder },
									{ path: 'paying', name: 'paying', component: Payingorder },
									{ path: 'waiting', name: 'waiting', component: Waitingorder },
									{ path: 'compelet', name: 'compelet', component:Compeletorder },
									] 
							}
						]
					},
					{
						path:'/order', name:'order',component:Order,children:[
							{ path: 'ordertopay/:money', name: 'ordertopay', component: Ordertopay },
							// router-link {{$route.params.xx}}()
							// 


							// id
							// Orderinfoordercomfirm
							{ path: 'orderinfo/:game/:type/:wechat/:area/:role/:more/:status/:goods/:count/:orderid/:money', name: 'orderinfo', component: Orderinfo },
							{ path: 'ordercomfirm/:account/:type/:game/:price/:role/:area/:count/:money/:password/', name: 'ordercomfirm', component: ordercomfirm },  
							{ path: 'orderwrite', name: 'orderwrite', component: Orderwrite }
						]
					},
					{
						path:'/mine', name:'mine',component:Mine,children:[
							{ path: 'charge', name: 'charge', component: Charge },
							{ path: 'chargedetail', name: 'chargedetail', component: chargeDetail },
							{ path: 'massege', component: massege,redirect:'msgOrder',children:[
									{ path: 'msgOrder', name: 'msgOrder', component: msgOrder },
									{ path: 'msgKefu', name: 'msgKefu', component: msgKefu },
									{ path: 'msgSys', name: 'msgSys', component: msgSys }
									] 
							},
							{ path: 'question', name: 'question', component: Question },
							{ path: 'cash', name: 'cash', component: Cash },
						]
					},
					{
						path:'/auth', name:'auth',component:Mine,children:[
							{ path: 'login', name: 'login', component: login },
							{ path: 'register', name: 'register', component: register },
						]
					}
				]

				

export default new VueRouter({ routes: routes,linkActiveClass:'mui-active' })


